.footer__divider {
	content: "";
	background-color: #000;
	opacity: 0.1;
	height: 2px;
	width: calc(100vw - 96px);
	margin: 50px auto 10px auto;
}
.footer__vertical-divider {
	content: "";
	background-color: #000;
	height: 1em;
	width: 2px;
	margin: 0 0.8rem;
	opacity: 0.6;
}
.footer__links {
	margin: 10px 0;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-size: small;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.footer__links li {
	list-style-type: none;
	align-items: center;
	display: flex;
	text-align: center;
}
.footer__link {
	margin: 0 auto;
}
.footer__copyright {
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	opacity: 0.5;
	margin-top: 10px;
	font-size: small;
}
.footer__container {
	padding: 24px 24px;
	display: flex;
	flex-direction: column;
	align-items: baseline;
	justify-content: flex-start;
}
.footer__social-media {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-left: 15px;
	margin-bottom: 10px;
}
.footer__social-media-link {
	margin-right: 15px;
	position: relative;
	cursor: pointer;
}
.footer__social-media-link img {
	height: 32px;
	width: 32px;
}
.footer-circle {
	position: absolute;
	top: 0;
	background: #000 no-repeat;
	border-radius: 50%;
	width: 32px;
	height: 32px;
	transform: scale(0);
	transition: all ease-out 0.3s;
	opacity: 0;
}
.footer__social-media-link:hover .footer-circle {
	transform-origin: center;
	transform: scale(1.5);
	opacity: 0.1;
}

@media screen and (max-width: 500px) {
	.footer__links {
		flex-direction: column;
		align-items: flex-start;
	}
	.footer__link {
		margin: 12px 0;
	}
	.footer__vertical-divider {
		display: none;
	}
}
