.header__background-mask {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: transparent linear-gradient(191deg, #413d3dc7 0%, #2a2628bd 100%)
		center no-repeat;
	background-size: 100% auto;
}
.header__background-container {
	position: relative;
	z-index: 0;
	width: 100vw;
	height: calc(100vw * 2000 / 4000);
	max-height: 500px;
}
.header__background {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
	background: transparent url("../../assets/background5.jpg") center no-repeat;
	background-size: 100% auto;
}
.header__background:before {
	position: absolute;
	content: "";
	height: 1px;
	width: 100%;
	background-color: transparent;
	box-shadow: 0 0 2px rgb(0, 0, 0), 0 0 4px rgb(0, 0, 0);
}
.header__brand {
	position: absolute;
	width: 100%;
	height: 100%;
	margin: auto;
	z-index: 1;

	background: transparent url("../../assets/brand.png") center no-repeat;
	background-size: 30% auto;
}
