.quote {
	display: flex;
	justify-content: flex-start;
	align-items: center;
}
.quote__matreshka {
	margin: 0 20px 0 auto;
	width: 114px;
}
.divider {
	margin: 60px 0;
	width: 100%;
	height: 30px;
	background: url("../../assets/divider.png") no-repeat center;
	background-size: 322px auto;
}
.quote__text {
	color: #000;
	font-family: Arial, Helvetica, sans-serif;
	font-weight: 500;
	line-height: 2em;
	max-width: 65%;
	margin-right: auto;
	padding-right: 5px;
	opacity: 0.4;
}

.menu {
	position: relative;
	width: 100%;
	height: 100%;
}
.dish {
	position: relative;
	top: 0;
	left: 0;
	margin: 20px 0;
	min-height: 300px;
	height: auto;
}
.dish img {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	margin: auto;
	padding: 10%;
	width: 50%;
	min-width: 30px;
	z-index: 2;
}
.dish__background {
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	content: "";
	width: 100%;
	height: 100%;
}
.dish__content {
	position: absolute;
	left: 50%;
	right: 20px;
	top: 20%;
	bottom: 10%;
	z-index: 2;
	display: flex;
	flex-direction: column;
	text-align: center;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.dish__content-header {
	width: 100%;
	height: auto;
	margin: 0 0 15px 0;

	font-weight: bold;
	font-size: 2em;
}
.dish__content-text {
	width: 100%;
	height: auto;
}
.dish.reversed img,
.dish__background.reversed {
	left: unset;
	right: 0;
}
.dish__content.reversed {
	margin: 10px 0 10px 10px;
	right: 50%;
	left: 20px;
}

@media screen and (max-width: 800px) {
	.quote__text {
		font-size: x-small;
	}
	.divider {
		background-size: 260px;
	}
}
@media screen and (max-width: 600px) {
	.dish__content {
		font-size: small;
	}
}
@media screen and (max-width: 400px) {
	.dish__content {
		font-size: x-small;
	}
	.dish {
		min-height: 250px;
	}
}
@media screen and (max-width: 250px) {
	.dish__content {
		display: none;
	}
	.dish img {
		width: 100%;
	}
}
