@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@800&family=Ubuntu:ital,wght@1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@500&family=Ubuntu:ital,wght@1,700&display=swap");

.navbar {
	z-index: 99;
	margin: 0;
	padding: 0;
	background-color: #fff;

	display: flex;
	justify-content: flex-start;
	align-items: center;
}
.navbar__logo {
	height: 100px;
	width: 100px;
}
.navbar__nav {
	width: 100vw;
}
.navbar__links {
	display: flex;
	width: 100%;
	justify-content: space-between;
	align-items: center;

	font-family: Mukta, Arial, Helvetica, sans-serif;
	font-weight: 800;
}
.navbar__links-left a {
	text-transform: uppercase;
	letter-spacing: 0.1em;
	margin-left: 1.5rem;
}
.navbar__links-right a {
	display: flex;
	align-items: center;
	margin-right: 2rem;
}
.navbar__links svg {
	height: 1.5rem;
	margin-right: 1rem;
}
.navbar__links a:hover {
	color: #d69a2d;
}
.navbar__links a:hover svg {
	fill: currentColor;
}
.navbar__menu {
	display: none;
	position: relative;
	cursor: pointer;
	width: 20px;
}
.navbar__menu-circle {
	position: absolute;
	top: -6px;
	right: -7.5px;
	width: 35px;
	height: 35px;
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	background: black;
	transition: all ease-out 0.2s;
	transform: scale(0);
	opacity: 0;
}
.navbar__menu:hover .navbar__menu-circle {
	transform-origin: center;
	transform: scale(1);
	opacity: 0.1;
}
.navbar__menu-bar,
.navbar__menu:after,
.navbar__menu:before {
	content: "";
	display: block;
	width: 100%;
	height: 2px;
	background-color: gray;
	margin: 4px 0px;
	transition: 0.4s;
}
.navbar__menu.active .navbar__menu-bar {
	opacity: 0;
}
.navbar__menu.active:after {
	transform: rotate(45deg) translate(-3.5px, -3.5px);
}
.navbar__menu.active:before {
	transform: rotate(-45deg) translate(-5px, 5px);
}
.navbar__menu-nav {
	position: fixed;
	padding-top: 5px;
	z-index: 99;
	bottom: auto;
	top: 64px;
	right: 0;
	left: 100vw;
	height: calc(100% - 64px);
	background-color: #fff;
	opacity: 0;
	box-shadow: inset 0 4px 3px -3px rgba(0, 0, 0, 0.1),
		inset 0 4px 2px -2px rgba(0, 0, 0, 0.07);

	transition: left 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.navbar__menu-mask {
	position: fixed;
	z-index: 98;
	top: 64px;
	bottom: 100vh;
	left: 0;
	right: 0;
	background: #000;
	opacity: 0;
	transition-duration: 0.3s;
	transition-property: opacity;
}
.navbar__menu-nav.navbar__links {
	opacity: 0;
}

@media screen and (max-width: 420px) {
	/*420px*/
	.navbar__links {
		font-weight: 500;
		flex-direction: column;
		font-size: 1.3em;
	}
	.navbar__links-left a:hover {
		color: #000;
	}
	.navbar__links-right a:hover {
		color: #d4a285;
	}
	.navbar__links-left {
		margin-top: 30px;
		width: 100%;
		display: flex;
		flex-direction: column;
	}
	.navbar__links-left a {
		text-transform: none;
		letter-spacing: 0.05em;
		color: #000;
		opacity: 0.8;
		margin: 10px 0;
	}
	.navbar__links-right {
		margin-top: 10px;
		padding: 0;
		width: 100%;
		opacity: 0.8;
	}
	.navbar__links svg {
		margin-right: 0.5rem;
	}
	.navbar__links-left:after {
		content: "";
		margin: 10px 0;
		width: 100%;
		height: 2px;
		background-color: #000;
		opacity: 0.1;
	}
	.navbar__links-right a {
		flex-wrap: nowrap;
		align-items: baseline;
		margin: 0;
	}
	.navbar__menu-nav.active {
		opacity: 1;
		left: 20vw;
		padding: 0 30px;
	}
	.navbar__menu-mask.active {
		opacity: 0.38;
		bottom: 0;
	}
	.navbar__logo {
		width: 64px;
		height: 64px;
	}
	.navbar__nav {
		display: none;
	}
	.navbar__menu {
		display: block;
		margin: 0 1rem 0 auto;
	}
}
